import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'KSS';
  online = false;
  constructor(){
  	this.online = navigator.onLine? true : false;
	console.log("online :",this.online);
  }
  
}



